.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #282c34;
  color: white;
}

.logo {
  font-size: 1.5em;
  font-weight: bold;
}

.nav {
  flex-grow: 1;
  text-align: right;
  margin-left: 2rem;
}

.nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 20px;
}

.nav-item {
  position: relative;
}

.nav-item a {
  color: white;
  text-decoration: none;
  font-size: 1.2em;
  padding: 5px;
  transition: color 0.3s;
}

.nav-item a::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background: white;
  transition: width 0.3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-item a:hover::after {
  width: 100%;
}

@media (max-width: 768px) {
  .nav-list {
      gap: 15px;
  }

  .nav-item a {
      font-size: 1em;
  }
}

@media (max-width: 480px) {
  .header {
      flex-direction: column;
      align-items: center;
  }

  .nav {
      width: 100%;
      text-align: left;
      margin-left: 0;
      margin-top: 10px;
  }

  .nav-list {
      flex-direction: column;
      align-items: center;
      gap: 10px;
  }

  .nav-item a {
      font-size: 1em;
      padding: 10px 0;
  }
}
