.about {
  padding: 50px 20px;
  background-color: #f5f5f5;
  color: #333;
}

.about-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
}

.about-image {
  flex: 1;
  max-width: 300px;
  margin-right: 40px;
  text-align: center;
}

.about-image img {
  width: 100%;
  border-radius: 50%;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

.about-image img:hover {
  transform: scale(1.05);
}

.about-content {
  flex: 2;
  min-width: 300px;
}

.about-content h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #282c34;
}

.about-content p {
  font-size: 1.2em;
  line-height: 1.6;
  margin-bottom: 20px;
  color: #555;
}

@media (max-width: 1024px) {
  .about-container {
      padding: 20px;
  }
  
  .about-content h2 {
      font-size: 2em;
  }

  .about-content p {
      font-size: 1.1em;
  }
}

@media (max-width: 768px) {
  .about-container {
      flex-direction: column;
      text-align: center;
      padding: 20px;
  }

  .about-image {
      margin: 0 0 30px 0;
  }

  .about-content {
      min-width: 100%;
  }
}

@media (max-width: 480px) {
  .about-content h2 {
      font-size: 1.8em;
  }

  .about-content p {
      font-size: 1em;
  }

  .about-image {
      max-width: 80%;
      margin: 0 auto 20px;
  }

  .about {
      padding: 30px 15px;
  }
}
