.project {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  margin-bottom: 20px;
  text-align: center;
  padding: 20px;
  max-height: 400px; /* Limiter la hauteur totale pour les grands écrans */
  display: flex;
  flex-direction: column;
}

.project-image {
  width: 100%;
  max-width: 300px;
  height: auto;
  border-radius: 10px;
  margin-bottom: 15px;
  object-fit: contain;
  cursor: pointer;
}

.project-details {
  text-align: left;
}

.project-title {
  font-size: 1.5em;
  margin-bottom: 10px;
  color: #333;
}

.project-description {
  font-size: 1em;
  margin-bottom: 15px;
  color: #555;
}

.project-technologies {
  font-size: 0.9em;
  color: black;
  margin-bottom: 15px;
  font-style: italic;
}

.project-link {
  font-size: 1em;
  color: #004187;
  text-decoration: none;
  transition: color 0.3s ease;
}

.project-link:hover {
  color: #0056b3;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  justify-content: center;
  align-items: center;
}

.modal img {
  max-width: 90%;
  max-height: 90%;
  border-radius: 10px;
}

.modal.open {
  display: flex;
}

.modal-content {
  position: relative;
}

.close {
  position: absolute;
  top: 10px;
  right: 15px;
  color: white;
  font-size: 30px;
  cursor: pointer;
}

@media (max-width: 768px) {
  .project {
    padding: 15px;
    max-height: 350px; /* Réduire la hauteur maximale pour les écrans moyens */
  }

  .project-title {
    font-size: 1.2em;
  }

  .project-description {
    font-size: 0.9em;
    max-height: 60px; /* Ajuster la hauteur pour les écrans moyens */
  }

  .project-technologies {
    font-size: 0.8em;
  }
}

@media (max-width: 480px) {
  .project {
    padding: 10px;
    max-height: 300px; /* Réduire davantage la hauteur maximale pour les petits écrans */
  }

  .project-title {
    font-size: 1em;
  }

  .project-description {
    font-size: 0.8em;
    max-height: 50px; /* Ajuster la hauteur pour les petits écrans */
  }

  .project-technologies {
    font-size: 0.7em;
  }

  .close {
    font-size: 24px;
    top: 5px;
    right: 10px;
  }
}
